body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.message {
  color: #E88D8D;
  font-family: "sans-serif";
  padding: 40px;
  position: absolute;
  bottom: 22%;
  opacity: 0; /* 初始状态为透明 */
  animation: fadeIn 3s forwards; /* 淡入动画，持续 2 秒 */
  font-weight: bold;
  text-align: center;
  line-height: 1.5;
  font-size: large;
}

@keyframes fadeIn {
  from {
    opacity: 0; /* 从完全透明开始 */
  }
  to {
    opacity: 1; /* 到完全不透明结束 */
  }
}